import qr from '@/components/tki-qrcode/tki-qrcode.vue';
import { UntieMa, commonman, GetCommon } from '@/api/supervisor/manager.js';
import { GetJiatinEWM } from '@/api/XFs.js';
import { setIDCard } from '@/api/user/userinfo.js';
export default {
	components: {
		qr
	},
	data() {
		return {
			providerList: [],
			sourceLink: 'http://yunzhujiao.cn/bind_pub/index.html',
			type: 0,
			val: '1111', // 要生成的二维码值
			size: 400, // 二维码大小
			unit: 'upx', // 单位
			background: '#b4e9e2', // 背景色
			foreground: '#309286', // 前景色
			pdground: '#32dbc6', // 角标色
			icon: '../../../../../static/shilu-login/ewmlogo.jpg', // 二维码图标
			iconsize: 40, // 二维码图标大小
			lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
			onval: false, // val值变化时自动重新生成二维码
			loadMake: false, // 组件加载完成后自动生成二维码
			src: '', // 二维码生成后的图片地址或base64
			address: '',
			cindex: 1,
			demo: 0,
			idCard: '',
			index: 0,
			index1: 0,
			modalName: null,
			count: '', //倒计时
			cardtype: [
				{
					name: '请选择',
					index: 0
				},
				{
					name: '居民身份证',
					index: '01'
				},
				{
					name: '居民户口簿',
					index: '02'
				},
				{
					name: '护照',
					index: '03'
				},
				{
					name: '军官证',
					index: '04'
				},
				{
					name: '驾驶证',
					index: '05'
				},
				{
					name: '港澳居民来往内地通行证',
					index: '06'
				},
				{
					name: '台湾居民来往内地通行证',
					index: '07'
				},
				{
					name: '出生医学证明',
					index: '08'
				},
				{
					name: '其他法定有效证件',
					index: '99'
				}
			],
			query: {
				name: '',
				idCardType: '',
				idCardValue: '',
				sex: '',
				tel: ''
			},
			msg: '',
			code: 0
		};
	},
	onLoad() {
		if (this.$store.state.userId == '') {
			uni.showModal({
				title: '提示',
				content: '请在微信授权后查看！',
				showCancel: true,
				success: function(rest) {
					if (rest.confirm) {
						uni.navigateTo({
							url: '/pages/login/login'
						});
					} else {
						uni.navigateBack({
							delta: 1,
							animationDuration: 200
						});
					}
				}
			});
		} else {
			if (this.$store.state.idCard != '' && this.$store.state.idCard != null) {
				this.demo = 2;
			}
			if (this.$store.state.HomeAddressUUID == null) {
				this.cindex = 1;
					this.demo=0;
			} else {
				this.cindex = 0;
			}
			if (this.cindex == 0) {
				this.onval = true;
				this.loadMake = true;
				GetJiatinEWM(this.$store.state.HomeAddressUUID).then(res => {
					console.log(res);

					if (res.data.code == 200) {
						this.val = res.data.data.split(',')[0];
						this.address = res.data.data.split(',')[1];
					}
				});
			}
		}
	},
	methods: {
		qrR(res) {
			this.src = res;
		},
		showModal(e) {
			if (this.$store.state.HomeAddressUUID == null) {
				uni.showToast({
					title: '请先绑定家庭码',
					icon: 'none'
				});
				return;
			}
			this.modalName = e.currentTarget.dataset.target;
		},
		claseModal(e){
			this.modalName=null;
		},
		hideModal(e) {
				uni.showToast({ title: '5秒后进入...', icon: 'success' });
				const time_count = 5;
				if (!this.timer) {
					this.count = time_count;
					this.show = false;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= time_count) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
							this.demo=1;
							this.modalName=null;
						}
					}, 1000);
				}
		},
		saoma() {
			uni.navigateTo({
				url: '/pages/YongHu/jiatinma'
			});
		},
		smbtn() {
			// uni.scanCode({
			// 	success: function(res) {
			// 		let s = encodeURIComponent(res.result);
			// 		commonman(s).then(res => {
			// 			console.log(res);
			// 		});
			// 	}
			// });
			uni.navigateTo({
				url: '/pages/user/info/common'
			});
		},
		PickerChange(e) {
			this.index = e.detail.value;
			this.query.idCardType = this.cardtype[e.detail.value].index;
		},
		PickerChange1(e) {
			this.index1 = e.detail.value;
			this.query.sex = this.sex[e.detail.value].index;
		},
		citizencode() {
			// uni.navigateTo({
			// 	url: '/pages/user/info/code'
			// });
		},
		untie() {
			let data = {
				guid: this.$store.state.userId
			};
			UntieMa(data).then(res => {
				if (res.data.code == 200) {
					this.cindex = 1;
					this.demo=0;
					this.$store.state.HomeAddressUUID = null;
					uni.showToast({ title: '解绑成功！' });
				} else {
					uni.showToast({ title: '解绑失败！', icon: 'none' });
				}
			});
		},
		//保存图片到相册
		save() {
			let that = this;
			uni.showActionSheet({
				itemList: ['保存图片到相册'],
				success: () => {
					uni.saveImageToPhotosAlbum({
						filePath: that.src,
						success: function() {
							uni.showToast({
								title: '保存成功',
								icon: 'none'
							});
						}
					});
				},
				fail() {
					uni.showToast({
						title: '保存失败，请重试！',
						icon: 'none'
					});
				}
			});
		},
		apply() {
			if (this.$store.state.HomeAddressUUID == null) {
				uni.showToast({
					title: '请先绑定家庭码',
					icon: 'none'
				});
				return;
			}
			if (this.query.name == '') {
				uni.showToast({
					title: '请输入姓名',
					icon: 'none'
				});
				return;
			}
			if (this.query.idCardType == '') {
				uni.showToast({
					title: '请选择证件类型',
					icon: 'none'
				});
				return;
			}
			if (this.query.tel == '') {
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none'
				});
				return;
			}
			let reg = /^[1-9]\d{5}(18|19|20|(3\d))\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
			if (!reg.test(this.idCard)) {
				uni.showToast({
					title: '请输入有效的身份证号',
					icon: 'none'
				});
				return;
			}
			let regs = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/;
			if (!regs.test(this.query.tel)) {
				uni.showToast({
					title: '请输入有效的手机号码',
					icon: 'none'
				});
				return;
			}
			this.query.idCardValue = this.idCard;
			GetCommon(this.query).then(res => {
				this.code = res.data.data.code;
				this.msg = res.data.data.msg;
				console.log(this.code);
				if (this.code != 1) {
					uni.showToast({
						title: this.msg,
						icon: 'none'
					});

					return;
				} else {
					let openid = uni.getStorageSync('openid');
					let data = {
						idCard: this.idCard,
						openid: openid,
						phone: this.query.tel,
						sex: this.query.sex,
						oldCard: this.query.idCardType,
						realName: this.query.name
					};
					setIDCard(data).then(res => {
						console.log(res);
						if (res.data.code == 200) {
							uni.showToast({
								title: res.data.message,
								icon: 'none'
							});
							this.demo = 2;
							this.$store.state.idCard = this.idCard;
						} else {
							uni.showToast({
								title: res.data.message,
								icon: 'none'
							});
						}
					});
				}
			});
		}
	}
};
